import { Input, Text } from 'native-base';

import { InlineField } from '../../../components/Forms';
import { BodyFatSelector } from '../../../components/Forms/BodyFatSelector';
import { ImperialHeightInput } from '../../../components/Forms/ImperialHeightInput';
import { UnitsType } from '../../../domain/constants';
import { TraineeStats } from '../../../domain/trainee';
import { getCircumferenceLabel, getHeightLabel, getWeightLabel } from '../../../utils/measurements';

export const CurrentBodyMetrics = ({
  unitsType,
  stats,
  setStat,
}: {
  unitsType: UnitsType;
  stats: TraineeStats;
  setStat: <K extends keyof TraineeStats>(preference: K, value: TraineeStats[K]) => void;
}) => {
  return (
    <>
      <InlineField>
        <Text>{`Your current weight (${getWeightLabel(unitsType)})`}</Text>
        <Input
          textAlign={'right'}
          width={50}
          keyboardType={'number-pad'}
          value={stats.weight?.toString()}
          onChangeText={(value: string) => setStat('weight', Number(value))}
        />
      </InlineField>
      <InlineField>
        <Text>{`Your waist circumference (${getCircumferenceLabel(unitsType)})`}</Text>
        <Input
          textAlign={'right'}
          width={50}
          keyboardType={'number-pad'}
          value={stats.waist?.toString()}
          onChangeText={(value: string) => setStat('waist', Number(value))}
        />
      </InlineField>
      <InlineField>
        <Text>{`Your height (${getHeightLabel(unitsType)})`}</Text>
        {unitsType === UnitsType.Imperial ? (
          <ImperialHeightInput height={stats.height} setHeight={(newValue: number) => setStat('height', newValue)} />
        ) : (
          <Input
            textAlign={'right'}
            width={50}
            keyboardType={'number-pad'}
            value={stats.height?.toString()}
            onChangeText={(value: string) => setStat('height', Number(value))}
          />
        )}
      </InlineField>
      <InlineField allowWrap>
        <Text>{`Bodyfat (%)`}</Text>
        <BodyFatSelector
          gender={stats.gender}
          bodyFat={stats.bodyFat}
          setBodyFat={(value: number) => {
            setStat('bodyFat', value);
          }}
        />
      </InlineField>
    </>
  );
};
