import { useMemo } from 'react';
import { useStoredReducer } from 'react-native-use-stored-state';

import { ActivityLevel, DietType, Gender, Goal, UnitsType, WeekDay } from '../constants';
import { Exercise, Lifts } from '../exercises';
import { getNutritionRecommendation, NutritionType } from '../nutrition';
import { getProgramRecommendations } from '../programs';
import { getSpeedLevel, getStrengthLevel } from '../standards';
import { calculateStrengthStandards } from '../standards';
import { getNextLiftTargets, getNextSpeedTargets } from './targets';

export interface TraineeStats {
  gender: keyof typeof Gender;
  age: number;
  weight: number;
  height: number;
  waist: number;
  activityLevel: keyof typeof ActivityLevel;
  bodyFat: number;
  everLifted: boolean;
  currentLifts: Lifts;
  mileRun: number;
  fiveKmRun: number;
  pushUps: number;
  pullUps: number;
  dips: number;
  needsMetabolicReset: boolean;
  currentCalories: number;
  dietType: keyof typeof DietType;
}

export interface TraineePreferences {
  unitsType: keyof typeof UnitsType;
  daysPerWeek: number;
  workoutDuration: number;
  primaryGoal: keyof typeof Goal;
  secondaryGoal: keyof typeof Goal;
  weekStart: keyof typeof WeekDay;
  workoutDays: Array<keyof typeof WeekDay>;
  cycleNutrition: boolean;
  higherCaloriesOnWeekends: boolean;
  preferAggressiveCut: boolean;
  cycleProgramsOften: boolean;
  minimalLegs: boolean;
  barbellIncrement: number;
  barbellDecrement: number;
  dumbbellIncrement: number;
  dumbbellDecrement: number;
}

const defaultStats = <TraineeStats>{
  gender: 'Male',
  age: 38,
  height: 176,
  weight: 75,
  bodyFat: 20,
  waist: 80,
  activityLevel: 'Sedentary',
  dietType: 'Balanced',
  currentLifts: {
    [Exercise.Squat]: {},
    [Exercise.Deadlift]: {},
    [Exercise.Bench]: {},
    [Exercise.Press]: {},
    [Exercise.Row]: {},
  },
};

const defaultPreferences = <TraineePreferences>{
  unitsType: 'Metric',
  daysPerWeek: 3,
  workoutDuration: 60,
  primaryGoal: 'Lean',
  secondaryGoal: 'Strength',
  weekStart: 'Monday',
  barbellIncrement: 2.5,
  barbellDecrement: 10,
  dumbbellIncrement: 2,
  dumbbellDecrement: 10,
};

export type TraineeStatsAction = keyof TraineeStats;

export const traineeStatsReducer = (stats: TraineeStats, action: { type: TraineeStatsAction; payload: any }) => {
  const { type, payload } = action;
  const newStats = { ...stats };

  switch (type) {
    default:
      newStats[type] = payload;
      return newStats;
  }
};

export type TraineePreferencesAction = keyof TraineePreferences;

export const traineePreferencesReducer = (
  preferences: TraineePreferences,
  action: { type: TraineePreferencesAction; payload: any }
) => {
  const { type, payload } = action;
  const newPrefs = { ...preferences };

  switch (type) {
    default:
      newPrefs[type] = payload;
      return newPrefs;
  }
};

export const useTrainee = () => {
  const [stats, updateStats, statsLoaded, unsetStats] = useStoredReducer<TraineeStats, TraineeStatsAction>(
    'TRAINEE_STATS_1.0.0',
    traineeStatsReducer,
    defaultStats
  );

  const [preferences, updatePreferences, preferencesLoaded, unsetPreferences] = useStoredReducer<
    TraineePreferences,
    TraineePreferencesAction
  >('TRAINEE_PREFS_1.0.1', traineePreferencesReducer, defaultPreferences);

  const setStat = <K extends keyof TraineeStats>(stat: K, value: TraineeStats[K]) => {
    updateStats({ type: stat, payload: value });
  };

  const setPreference = <K extends keyof TraineePreferences>(preference: K, value: TraineePreferences[K]) => {
    updatePreferences({ type: preference, payload: value });
  };

  const resetTrainee = () => {
    unsetPreferences();
    unsetStats();
  };

  const strengthStandards = useMemo(() => stats && calculateStrengthStandards(stats.weight, stats.currentLifts), [stats]);

  const strengthLevel = useMemo(
    () => stats && strengthStandards && getStrengthLevel(Gender[stats.gender], stats.everLifted, strengthStandards),
    [stats, strengthStandards]
  );

  const speedLevel = useMemo(() => stats && getSpeedLevel(Gender[stats.gender], stats.mileRun, stats.fiveKmRun), [stats]);

  const recommendedPrograms = useMemo(
    () =>
      stats &&
      preferences &&
      getProgramRecommendations({
        gender: Gender[stats.gender],
        strengthLevel: strengthLevel!,
        primaryGoal: Goal[preferences.primaryGoal],
        secondaryGoal: Goal[preferences.secondaryGoal],
        maxDaysPerWeek: preferences.daysPerWeek,
        maxWorkoutDuration: preferences.workoutDuration,
        speedLevel: speedLevel!,
      }),
    [stats, preferences, speedLevel, strengthLevel]
  );

  const recommendedNutrition = useMemo(
    () =>
      stats &&
      preferences &&
      getNutritionRecommendation({
        gender: Gender[stats.gender],
        weight: stats.weight,
        height: stats.height,
        age: stats.age,
        unitsType: UnitsType[preferences.unitsType],
        bodyFat: stats.bodyFat,
        activityLevel: ActivityLevel[stats.activityLevel],
        primaryGoal: Goal[preferences.primaryGoal],
        secondaryGoal: Goal[preferences.secondaryGoal],
        needsMetabolicReset: stats.needsMetabolicReset,
        currentCalories: stats.currentCalories,
        higherCaloriesOnWeekends: preferences.higherCaloriesOnWeekends,
        cycleNutrition: preferences.cycleNutrition,
        numberOfWorkoutDays: preferences.daysPerWeek, // @TODO should be the selected program
        preferAggressiveCut: preferences.preferAggressiveCut,
      }),
    [stats, preferences]
  );

  const speedTargets = useMemo(
    () => stats && getNextSpeedTargets(Gender[stats.gender], { mileRun: stats.mileRun, fiveKmRun: stats.fiveKmRun }),
    [stats]
  );

  const liftTargets = useMemo(
    () => stats && strengthStandards && getNextLiftTargets(Gender[stats.gender], strengthStandards, stats.weight),
    [stats, strengthStandards]
  );

  // const bodyweightTargets = useMemo(() => {}, [stats]);

  const bodyFatTarget =
    stats &&
    stats.bodyFat > 10 &&
    recommendedNutrition &&
    [NutritionType.Cut, NutritionType.AggressiveCut].includes(recommendedNutrition.type)
      ? stats.bodyFat - 5
      : undefined;

  return {
    traineeLoaded: statsLoaded && preferencesLoaded,
    stats: stats ?? defaultStats,
    setStat,
    preferences: preferences ?? defaultPreferences,
    setPreference,
    resetTrainee,
    strengthStandards,
    strengthLevel,
    speedLevel,
    recommendedNutrition,
    recommendedPrograms,
    speedTargets,
    liftTargets,
    bodyFatTarget,
  };
};;;
