import { Center } from 'native-base';

import { Video } from '../../components/Content';
import { DashboardCard, DashboardDetail } from '../../components/Dashboards';
import { HeadedLayout } from '../../components/Layouts';
import { Loader } from '../../components/Loader';
import { useActiveProgram } from '../../domain/programs/activeProgram';
import { useTrainee } from '../../domain/trainee';
import { Schedule } from './Schedule';
import { Workouts } from './Workouts';

export default function Program() {
  const { strengthLevel } = useTrainee();
  const { activeProgramLoaded, program, schedule } = useActiveProgram();

  if (!activeProgramLoaded) {
    return <Loader />;
  }
  return (
    <HeadedLayout showNav heading="Program">
      <DashboardCard heading={'Active Program'}>
        <DashboardDetail label="Name" value={program!.name} />
        <DashboardDetail label="Author" value={program!.author} />
        <DashboardDetail label="Sourced from" value={program!.link} />
        <DashboardDetail label="Duration" value={`${program!.duration} weeks`} />
        <DashboardDetail label="Workouts per week" value={program!.daysPerWeek} />
        <DashboardDetail label="Workout duration" value={`${program!.workoutDuration} min`} />
        {program?.video && (
          <Center paddingTop={2}>
            <Video video={program.video} />
          </Center>
        )}
      </DashboardCard>
      <Workouts workouts={program!.workouts(strengthLevel)} />
      <Schedule schedule={schedule!} />
    </HeadedLayout>
  );
}
