import { Box, Divider, Text } from 'native-base';
import { NutritionProtocol, NutritionType } from '../../domain/nutrition';

import { DashboardDetail } from '../Dashboards';

export const NutritionInfo = ({ protocol }: { protocol: NutritionProtocol }) => {
  const { note, bmr, tdee } = protocol;
  return (
    <>
      {note && <Text fontSize={'xs'}>{note}</Text>}
      <Box marginY={2}>
        <DashboardDetail label={'BMR (Base Metabolic Rate)'} value={`${bmr}`} />
        <Divider />
        <DashboardDetail label={'TDEE (Total Daily Energy Expenditure)'} value={`${tdee}`} />
        <Divider />
        <NutritionRecommendations protocol={protocol} />
        <Box marginY={2}>
          <Text fontSize={'2xs'} italic>
            NOTE: BMR was calculated using the average of Miffin St Jeor, Harris Benedict and Katch McArdle formulas. TDEE was
            calculated using your BMR and given activity level (lightly active=x1.375, active=x1.55 and very active=x1.725).
            Minimum protein to consume was calculated using your body fat % (1 gram per pound of lean bodyweight)
          </Text>
        </Box>
      </Box>
    </>
  );
};

export const NutritionRecommendations = ({ protocol }: { protocol: NutritionProtocol }) => {
  const { targetCalories, type, higherCaloriesOnWeekends, cycleNutrition, targetProtein } = protocol;
  return (
    <Box>
      <DashboardDetail
        label={`Recommended Target Average ${type === NutritionType.Bulk ? 'Surplus' : 'Deficit'}`}
        value={`${targetCalories.energyExpenditureModifier}`}
      />
      <Divider />
      <DashboardDetail label={`Target Average Daily Calories`} value={`${targetCalories.average}`} />
      <Divider />
      {higherCaloriesOnWeekends && !cycleNutrition && (
        <>
          <DashboardDetail label={`   Weekday Calories`} value={`${targetCalories.trainingDay}`} />
          <Divider />
          <DashboardDetail label={`   Weekend Calories`} value={`${targetCalories.weekend}`} />
          <Divider />
        </>
      )}
      {cycleNutrition && !higherCaloriesOnWeekends && (
        <>
          <DashboardDetail label={`   Training Day Calories`} value={`${targetCalories.trainingDay}`} />
          <Divider />
          <DashboardDetail label={`   Rest Day Calories`} value={`${targetCalories.restDay}`} />
          <Divider />
        </>
      )}
      {cycleNutrition && higherCaloriesOnWeekends && (
        <>
          <DashboardDetail label={`   Weekday Training Day Calories`} value={`${targetCalories.trainingDay}`} />
          <Divider />
          <DashboardDetail label={`   Weekday Rest Day Calories`} value={`${targetCalories.restDay}`} />
          <Divider />
          <DashboardDetail label={`   Weekend Calories`} value={`${targetCalories.weekend}`} />
          <Divider />
        </>
      )}
      <DashboardDetail label={`Minimum Grams of Protein`} value={`${targetProtein}`} />
      <Divider />
    </Box>
  );
};
