import { HeadedLayout } from '../../components/Layouts';
import { Loader } from '../../components/Loader';
import Today from './Today';
import { useTrainee } from '../../domain/trainee';
import { useActiveProgram } from '../../domain/programs/activeProgram';
import ThisWeek from './ThisWeek';

export default function Dashboard() {
  const { traineeLoaded, recommendedNutrition } = useTrainee();
  const { activeProgramLoaded, todaysWorkout, thisWeeksWorkouts } = useActiveProgram();

  if (!traineeLoaded || !activeProgramLoaded) {
    return <Loader />;
  }

  return (
    <HeadedLayout showNav heading="Dashboard">
      <Today recommendedNutrition={recommendedNutrition!} workout={todaysWorkout} />
      <ThisWeek workouts={thisWeeksWorkouts} />
    </HeadedLayout>
  );
}
