import { Alert, Button, HStack, Input, Select, Switch, Text } from 'native-base';

import { useTrainee } from '../../domain/trainee';
import { EnumSelect, FormCard, InlineField } from '../../components/Forms';
import { DefaultLayout } from '../../components/Layouts';
import { Loader } from '../../components/Loader';
import { ActivityLevel, DietType, Gender, Goal, UnitsType } from '../../domain/constants';
import { LiftingStandardsCalculator } from '../../components/Standards';
import { Destination, RoutingButton } from '../../utils/routing';
import { NutritionPreferences } from './components/NutritionPreferences';
import { ProgressionPreferences } from './components/ProgressionPreferences';
import { SchedulingPreferences } from './components/SchedulingPreferences';
import { CurrentBodyMetrics } from './components/CurrentBodyMetrics';
import { Lifts } from '../../domain/exercises';

export default function GetStarted() {
  const { traineeLoaded, stats, setStat, preferences, setPreference, strengthStandards } = useTrainee();

  if (!traineeLoaded) {
    return <Loader />;
  }

  const enableContinueButton =
    !!stats.bodyFat &&
    !!stats.weight &&
    !!preferences.primaryGoal &&
    !!preferences.secondaryGoal &&
    !!preferences.daysPerWeek &&
    !!preferences.workoutDuration;

  return (
    <DefaultLayout>
      <FormCard heading={'Configuration'}>
        <InlineField>
          <Text>Units</Text>
          <EnumSelect
            Enum={UnitsType}
            selectedValue={preferences.unitsType}
            onValueChange={(value: keyof typeof UnitsType) => setPreference('unitsType', value)}
          />
        </InlineField>
      </FormCard>

      <FormCard heading={'About You'}>
        <InlineField>
          <Text>Gender</Text>
          <EnumSelect
            Enum={Gender}
            selectedValue={stats.gender}
            onValueChange={(value: keyof typeof Gender) => setStat('gender', value)}
          />
        </InlineField>
        <InlineField>
          <Text>{`Your age`}</Text>
          <Input
            textAlign={'right'}
            width={50}
            keyboardType={'number-pad'}
            value={stats.age?.toString()}
            onChangeText={(value: string) => setStat('age', Number(value))}
          />
        </InlineField>
        <CurrentBodyMetrics stats={stats} setStat={setStat} unitsType={UnitsType[preferences.unitsType]} />
      </FormCard>

      <FormCard heading={'Your current nutrition and preferences'}>
        <InlineField>
          <Text>{'Eating style'}</Text>
          <EnumSelect
            Enum={DietType}
            selectedValue={stats.dietType}
            onValueChange={(value: keyof typeof DietType) => setStat('dietType', value)}
          />
        </InlineField>
        <InlineField>
          <Text>{'Have you been "dieting" on low calories for a long time without results?'}</Text>
          <Select
            selectedValue={stats.needsMetabolicReset ? 'yes' : 'no'}
            placeholder={'Select...'}
            onValueChange={(value: string) => setStat('needsMetabolicReset', value === 'yes')}
            minWidth={'200'}
          >
            <Select.Item value={'no'} label={'No'} />
            <Select.Item value={'yes'} label={'Yes'} />
          </Select>
        </InlineField>
        {stats.needsMetabolicReset && (
          <InlineField>
            <Text>{`Roughly how many calories?`}</Text>
            <Input
              textAlign={'right'}
              width={60}
              keyboardType={'decimal-pad'}
              value={stats.currentCalories?.toString()}
              onChangeText={(value: string) => setStat('currentCalories', Number(value))}
            />
          </InlineField>
        )}
        <NutritionPreferences preferences={preferences} setPreference={setPreference} />
      </FormCard>

      <FormCard heading={'Your current activity'}>
        <InlineField>
          <Text>General daily activity</Text>
          <EnumSelect
            Enum={ActivityLevel}
            selectedValue={stats.activityLevel}
            onValueChange={(value: keyof typeof ActivityLevel) => setStat('activityLevel', value)}
          />
        </InlineField>
        <InlineField>
          <Text>{`Mile run time (mins)`}</Text>
          <Input
            textAlign={'right'}
            width={50}
            keyboardType={'decimal-pad'}
            value={stats.mileRun?.toString()}
            onChangeText={(value: string) => setStat('mileRun', Number(value))}
          />
        </InlineField>
        <InlineField>
          <Text>{`5k run time (mins)`}</Text>
          <Input
            textAlign={'right'}
            width={50}
            keyboardType={'decimal-pad'}
            value={stats.fiveKmRun?.toString()}
            onChangeText={(value: string) => setStat('fiveKmRun', Number(value))}
          />
        </InlineField>
        <InlineField>
          <Text>{`How many push ups can you do?`}</Text>
          <Input
            textAlign={'right'}
            width={50}
            keyboardType={'decimal-pad'}
            value={stats.pushUps?.toString()}
            onChangeText={(value: string) => {
              setStat('pushUps', Number(value));
            }}
          />
        </InlineField>
        <InlineField>
          <Text>{`How many pull ups can you do?`}</Text>
          <Input
            textAlign={'right'}
            width={50}
            keyboardType={'decimal-pad'}
            value={stats.pullUps?.toString()}
            onChangeText={(value: string) => {
              setStat('pullUps', Number(value));
            }}
          />
        </InlineField>
        <InlineField>
          <Text>Ever done barbell lifting?</Text>
          <Select
            selectedValue={stats.everLifted ? 'yes' : 'no'}
            placeholder={'Select...'}
            onValueChange={(value: string) => setStat('everLifted', value === 'yes')}
            minWidth={'200'}
          >
            <Select.Item value={'yes'} label={'Yes'} />
            <Select.Item value={'no'} label={'No'} />
          </Select>
        </InlineField>
        {stats.everLifted && (
          <LiftingStandardsCalculator
            unitsType={UnitsType[preferences.unitsType as keyof typeof UnitsType]}
            lifts={stats.currentLifts}
            standards={strengthStandards}
            setLifts={(newLifts: Lifts) => {
              setStat('currentLifts', newLifts);
            }}
          />
        )}
      </FormCard>

      <FormCard heading={'Your goals and preferences'}>
        <InlineField>
          <Text>Primary Goal</Text>
          <EnumSelect
            Enum={Goal}
            selectedValue={preferences.primaryGoal}
            onValueChange={(value: keyof typeof Goal) => setPreference('primaryGoal', value)}
          />
        </InlineField>
        <InlineField>
          <Text>Secondary Goal</Text>
          <EnumSelect
            Enum={Goal}
            selectedValue={preferences.secondaryGoal}
            onValueChange={(value: keyof typeof Goal) => setPreference('secondaryGoal', value)}
          />
        </InlineField>
        <InlineField>
          <Text maxWidth={'85%'}>
            <Text bold>Mix it up often</Text>: If you get bored easily, this will cycle you through appropriate programs more
            often
          </Text>
          <Switch
            size="sm"
            isChecked={preferences.cycleProgramsOften}
            onValueChange={(checked: boolean) => setPreference('cycleProgramsOften', checked)}
          />
        </InlineField>
        <InlineField>
          <Text maxWidth={'85%'}>
            <Text bold>Minimal legs</Text>: For those not keen on squatting heavy and often, or those who just do not want big
            legs and rather keep them strong and athletic
          </Text>
          <Switch
            size="sm"
            isChecked={preferences.minimalLegs}
            onValueChange={(checked: boolean) => setPreference('minimalLegs', checked)}
          />
        </InlineField>
      </FormCard>

      <FormCard heading={'Scheduling & Progression'}>
        <Alert status={'info'} colorScheme={'info'}>
          <HStack flexShrink={1} space={2}>
            <Text>
              It is important you commit to something sustainable, more is not always better. Success comes from long term
              consistency.
            </Text>
          </HStack>
        </Alert>
        <SchedulingPreferences preferences={preferences} setPreference={setPreference} />
        <ProgressionPreferences preferences={preferences} setPreference={setPreference} />
      </FormCard>

      {enableContinueButton ? (
        <RoutingButton to={Destination.ProgramSelection}>Continue</RoutingButton>
      ) : (
        <Button colorScheme="secondary" disabled>
          Continue
        </Button>
      )}
    </DefaultLayout>
  );
}
