import { ArrowDownIcon, ArrowUpIcon, Box, HStack, Input, Text } from 'native-base';
import { InlineField } from '../../../components/Forms';
import { UnitsType } from '../../../domain/constants';
import { TraineePreferences } from '../../../domain/trainee';
import { getWeightLabel } from '../../../utils/measurements';

export const ProgressionPreferences = ({
  preferences,
  setPreference,
}: {
  preferences: TraineePreferences;
  setPreference: <K extends keyof TraineePreferences>(preference: K, value: TraineePreferences[K]) => void;
}) => {
  const weightUnitLabel = getWeightLabel(UnitsType[preferences.unitsType]);
  const percentLabel = '%';
  return (
    <>
      <Box paddingY={1}>
        <Text>
          On Success <ArrowUpIcon />
        </Text>
        <InlineField>
          <Text>Barbell Increment</Text>
          <HStack space={1} alignItems={'center'}>
            <Input
              textAlign={'right'}
              width={50}
              keyboardType={'decimal-pad'}
              value={preferences.barbellIncrement?.toString()}
              // @ts-expect-error if cast to string it doesn't let you do decimals because it casts before typing all
              onChangeText={(value: string) => setPreference('barbellIncrement', value)}
            />
            <Text>{weightUnitLabel}</Text>
          </HStack>
        </InlineField>
        <InlineField>
          <Text>Dumbbell Increment</Text>
          <HStack space={1} alignItems={'center'}>
            <Input
              textAlign={'right'}
              width={50}
              keyboardType={'decimal-pad'}
              value={preferences.dumbbellIncrement?.toString()}
              // @ts-expect-error if cast to string it doesn't let you do decimals because it casts before typing all
              onChangeText={(value: string) => setPreference('dumbbellIncrement', value)}
            />
            <Text>{weightUnitLabel}</Text>
          </HStack>
        </InlineField>
      </Box>
      <Box paddingY={1}>
        <Text>
          On Failure <ArrowDownIcon />
        </Text>
        <InlineField>
          <Text>Barbell Decrement</Text>
          <HStack space={1} alignItems={'center'}>
            <Input
              textAlign={'right'}
              width={50}
              keyboardType={'numeric'}
              value={preferences.barbellDecrement?.toString()}
              onChangeText={(value: string) => setPreference('barbellDecrement', Number(value))}
            />
            <Text>{percentLabel}</Text>
          </HStack>
        </InlineField>
        <InlineField>
          <Text>Dumbbell Decrement</Text>
          <HStack space={1} alignItems={'center'}>
            <Input
              textAlign={'right'}
              width={50}
              keyboardType={'numeric'}
              value={preferences.dumbbellDecrement?.toString()}
              onChangeText={(value: string) => setPreference('dumbbellDecrement', Number(value))}
            />
            <Text>{percentLabel}</Text>
          </HStack>
        </InlineField>
      </Box>
    </>
  );
};
