import { HStack, Input, Text } from 'native-base';
import { useEffect, useState } from 'react';
import { feetToInches, inchesToFeet } from '../../../utils/measurements';

export const ImperialHeightInput = ({ height, setHeight }: { height: number; setHeight: (newvalue: number) => void }) => {
  const [feet, setFeet] = useState<number>();
  const [inches, setInches] = useState<number>();

  const onSetHeight = () => {
    if (feet && inches) {
      const heightInInches = feetToInches(feet) + inches;
      setHeight(heightInInches);
    }
  };

  useEffect(() => {
    if (height) {
      const converted = inchesToFeet(height);
      const feet = Math.floor(converted);
      const inches = Math.round(feetToInches(converted - feet));
      setFeet(feet);
      setInches(inches);
    }
  }, []);

  useEffect(() => {
    onSetHeight();
  }, [feet, inches]);

  return (
    <HStack alignItems={'center'}>
      <Input
        textAlign={'right'}
        width={50}
        keyboardType={'number-pad'}
        value={feet?.toString()}
        marginX={1}
        onChangeText={(value: string) => {
          setFeet(Number(value));
        }}
      />
      <Text>{'ft'}</Text>
      <Input
        textAlign={'right'}
        width={50}
        keyboardType={'number-pad'}
        value={inches?.toString()}
        marginX={1}
        onChangeText={(value: string) => {
          setInches(Number(value));
        }}
      />
      <Text>{'in'}</Text>
    </HStack>
  );
};
