import { useEffect, useState } from 'react';
import { Lifts } from '../exercises';
import { useLog } from '../log';

import { useActiveProgram } from '../programs/activeProgram';
import { useTrainee } from '../trainee';
import {
  getWorkoutById,
  getWorkoutTotalSetCount,
  getRecommendedWeight,
  getCompleteSetCount,
  getPreviousLift,
  updateCurrentLifts,
} from './helpers';

export const useWorkout = (id: number) => {
  const { stats, setStat, preferences, strengthStandards } = useTrainee();
  const { logWorkout } = useLog();
  const { program, schedule, updateSchedule } = useActiveProgram();

  const [setsDone, setSetsDone] = useState<number>(0);

  const workout = schedule && getWorkoutById(schedule)(Number(id));
  const link = `/Workout/${workout?.id}`;
  const totalSets = workout ? getWorkoutTotalSetCount(workout) : 0;

  useEffect(() => {
    schedule && setSetsDone(getCompleteSetCount(workout!));
  }, [id, schedule, setSetsDone]);

  const startWorkout = () => {
    updateSchedule({ type: 'start_workout', payload: { id } });
  };

  const finishWorkout = () => {
    updateCurrentLifts(stats.currentLifts, workout!, (newLifts: Lifts) => setStat('currentLifts', newLifts));
    updateSchedule({ type: 'end_workout', payload: { id } });
    logWorkout(workout!, program!.name);
  };

  const getSetRecommendedWeight = getRecommendedWeight({
    schedule: schedule!,
    workoutId: id,
    barbellIncrement: preferences.barbellIncrement,
    barbellDecrement: preferences.barbellDecrement,
    dumbbellIncrement: preferences.dumbbellIncrement,
    dumbbellDecrement: preferences.dumbbellDecrement,
    strengthStandards: strengthStandards!,
    bodyweight: stats.weight!,
    currentLifts: stats.currentLifts,
  });

  const onSetComplete = (exerciseId: number, setId: number, isWarmup: boolean) => {
    updateSchedule({
      type: 'complete_set',
      payload: { id, exerciseId, setId, isWarmup, recommendedWeight: getSetRecommendedWeight(exerciseId, setId, isWarmup) },
    });
    setSetsDone((previous: number) => previous + 1);
  };

  const onChangeSetWeight = (exerciseId: number, setId: number, value: number) => {
    updateSchedule({ type: 'change_set_weight', payload: { id, exerciseId, setId, value } });
  };

  const onChangeSetReps = (exerciseId: number, setId: number, value: number) => {
    updateSchedule({ type: 'change_set_reps', payload: { id, exerciseId, setId, value } });
  };

  const getRestTime = (exerciseId: number, setId: number, isWarmup: boolean) => {
    if (isWarmup) return 60;
    return (schedule![id].routine[exerciseId].rest ?? 1) * 60;
  };

  return {
    workout,
    totalSets,
    setsDone,
    startWorkout,
    link,
    finishWorkout,
    getRecommendedWeight: getSetRecommendedWeight,
    getPreviousLift: getPreviousLift(schedule!, id, stats.currentLifts),
    onChangeSetWeight,
    onChangeSetReps,
    onSetComplete,
    getRestTime,
  };
};
