import { Text, Switch } from 'native-base';

import { InlineField } from '../../../components/Forms';
import { TraineePreferences } from '../../../domain/trainee';

export const NutritionPreferences = ({
  preferences,
  setPreference,
}: {
  preferences: TraineePreferences;
  setPreference: <K extends keyof TraineePreferences>(preference: K, value: TraineePreferences[K]) => void;
}) => (
  <>
    <InlineField>
      <Text maxWidth={'85%'}>
        <Text bold>Consume higher calories on the weekends</Text>: for those who prefer to eat less during the week and indulge
        more on the weekend and still meet their goals
      </Text>
      <Switch
        size="sm"
        isChecked={preferences.higherCaloriesOnWeekends}
        onValueChange={(checked: boolean) => setPreference('higherCaloriesOnWeekends', checked)}
      />
    </InlineField>
    <InlineField>
      <Text maxWidth={'85%'}>
        <Text bold>Cycle calories</Text>: for those who prefer to eat more on training days and less on rest days
      </Text>
      <Switch
        size="sm"
        isChecked={preferences.cycleNutrition}
        onValueChange={(checked: boolean) => setPreference('cycleNutrition', checked)}
      />
    </InlineField>
  </>
);
