import { Text } from 'native-base';
import { useMemo } from 'react';

import { DashboardCard } from '../../components/Dashboards';
import { WorkoutList } from '../../components/Workouts';
import { ScheduledWorkout } from '../../domain/schedule';

export const Schedule = ({ schedule }: { schedule: ScheduledWorkout[] }) => {
  const completedCount = useMemo(() => schedule.filter((item) => item.status === 'done').length, [schedule]);

  return (
    <DashboardCard heading={'Schedule'}>
      <WorkoutList workouts={schedule} />
      <Text italic textAlign={'right'}>
        {completedCount}/{schedule.length} workouts complete
      </Text>
    </DashboardCard>
  );
};
