import { Select, Text } from 'native-base';

import { EnumMultiSelect, EnumSelect, InlineField, StackedField } from '../../../components/Forms';
import { WeekDay } from '../../../domain/constants';
import { TraineePreferences } from '../../../domain/trainee';

export const SchedulingPreferences = ({
  preferences,
  setPreference,
}: {
  preferences: TraineePreferences;
  setPreference: <K extends keyof TraineePreferences>(preference: K, value: TraineePreferences[K]) => void;
}) => {
  return (
    <>
      <InlineField>
        <Text>Workouts per week</Text>
        <Select
          selectedValue={preferences.daysPerWeek?.toString()}
          placeholder={'Select...'}
          onValueChange={(value: string) => setPreference('daysPerWeek', Number(value))}
          minWidth={'200'}
        >
          <Select.Item value={'2'} label={'2 at most'} />
          <Select.Item value={'3'} label={'3 at most'} />
          <Select.Item value={'4'} label={'4 at most'} />
          <Select.Item value={'5'} label={'5 at most'} />
          <Select.Item value={'6'} label={'6 at most'} />
        </Select>
      </InlineField>
      <InlineField>
        <Text>Workout duration</Text>
        <Select
          selectedValue={preferences.workoutDuration?.toString()}
          placeholder={'Select...'}
          onValueChange={(value: string) => setPreference('workoutDuration', Number(value))}
          minWidth={'200'}
        >
          <Select.Item value={'30'} label={'30 minutes at most'} />
          <Select.Item value={'45'} label={'45 minutes at most'} />
          <Select.Item value={'60'} label={'60 minutes at most'} />
          <Select.Item value={'75'} label={'75 minutes at most'} />
          <Select.Item value={'90'} label={'90 minutes at most'} />
        </Select>
      </InlineField>
      <InlineField>
        <Text>Start week on</Text>
        <EnumSelect
          Enum={WeekDay}
          selectedValue={preferences.weekStart}
          onValueChange={(value: keyof typeof WeekDay) => setPreference('weekStart', value)}
        />
      </InlineField>
      <StackedField>
        <Text>Days you can train</Text>
        <EnumMultiSelect
          Enum={WeekDay}
          accessibilityLabel={'Select days you can train'}
          selectedValues={preferences.workoutDays}
          onChange={(newValues: Array<keyof typeof WeekDay>) => setPreference('workoutDays', newValues)}
        />
      </StackedField>
    </>
  );
};
