import { Box, Divider, HStack, Heading, Text, Center } from 'native-base';
import { ScrollView } from 'react-native';

import { Video } from '../../components/Content';
import { DashboardCard } from '../../components/Dashboards';
import { RoutineEntry, Workout } from '../../domain/programs';

export const getExerciseRepLabel = (item: RoutineEntry): string => {
  const sets = item.sets.length > 0 ? item.sets : item.warmup;
  const numberOfSets = sets?.length;
  let targetReps = `${sets?.[0]?.reps || sets?.[0]?.targetReps}`;
  if (sets?.[0]?.minReps) {
    targetReps = `${sets?.[0]?.minReps}-${sets?.[0]?.maxReps}`;
  }
  return numberOfSets && targetReps ? `${numberOfSets}x${targetReps}` : '';
};

export const Workouts = ({ workouts }: { workouts: Workout[] }) => (
  <DashboardCard heading={'Workouts'}>
    <ScrollView horizontal={true}>
      <HStack>
        {workouts.map((workout: Workout, idx) => {
          const wid = `workout-${workout.name}`;
          const hasDivider = idx < workouts.length - 1;
          return (
            <HStack key={wid}>
              <Box width={260}>
                <Heading fontSize={'md'} fontWeight={300} paddingBottom={2}>
                  {workout.name}
                </Heading>
                {workout.routine.map((item: RoutineEntry) => (
                  <Box key={`${wid}-${item.exercise}`} paddingBottom={1}>
                    <Text bold>{item.exercise}</Text>
                    <Text>{getExerciseRepLabel(item)}</Text>
                  </Box>
                ))}
                {workout.video && (
                  <Center paddingTop={2}>
                    <Video video={workout.video} width={280} />
                  </Center>
                )}
              </Box>
              {hasDivider && <Divider marginX={4} orientation={'vertical'} />}
            </HStack>
          );
        })}
      </HStack>
    </ScrollView>
  </DashboardCard>
);
