import { Text } from 'native-base';

import { UnitsType } from '../../domain/constants';
import { StrengthMetrics, StrengthStandards } from '../../domain/standards';
import { getWeightLabel } from '../../utils/measurements';
import { toNearestIncrement } from '../../utils/weight';
import { BulletPoint } from '../Content';

export const LiftingTargets = ({
  liftTargets,
  unitsType,
  barbellIncrement,
}: {
  liftTargets: StrengthStandards;
  unitsType: UnitsType;
  barbellIncrement: number;
}) => {
  const weightLabel = getWeightLabel(unitsType);
  return (
    <>
      {Object.keys(liftTargets).map((lift) => {
        const { oneRepMax, fiveRepMax, reps } = liftTargets[lift as keyof typeof liftTargets] as StrengthMetrics;
        const roundedFiveReps = fiveRepMax && toNearestIncrement(fiveRepMax, barbellIncrement);

        return (
          <BulletPoint key={lift}>
            <Text fontSize={'xs'}>
              {oneRepMax ? (
                <>
                  Get your {lift} 1RM to {oneRepMax}
                  {weightLabel}{' '}
                  {roundedFiveReps && (
                    <Text>
                      (that&apos;s {roundedFiveReps} {weightLabel} for 5 reps)
                    </Text>
                  )}
                </>
              ) : (
                <>
                  Get your number of {lift} to {reps}
                </>
              )}
            </Text>
          </BulletPoint>
        );
      })}
    </>
  );
};
