import { Text } from 'native-base';

import { Paragraph } from '../../components/Content';
import { DashboardCard } from '../../components/Dashboards';
import { getTodaysCalories, NutritionProtocol } from '../../domain/nutrition';
import { ScheduledWorkout } from '../../domain/schedule';
import { useWorkout } from '../../domain/workout';
import { RoutingButton } from '../../utils/routing';

export default function Today({
  recommendedNutrition,
  workout,
}: {
  recommendedNutrition: NutritionProtocol;
  workout: ScheduledWorkout | undefined;
}) {
  const calories = getTodaysCalories(recommendedNutrition, !!workout);
  const { startWorkout, link } = useWorkout(Number(workout?.id));

  return (
    <DashboardCard heading={'Today'}>
      {workout ? (
        <Paragraph>
          <Text paddingRight={3}>
            Today you are scheduled to perform <Text bold>{workout.name}</Text>.
          </Text>
          {workout.status === 'in progress' && <RoutingButton to={link}>Continue Workout</RoutingButton>}
          {workout.status === 'pending' && (
            <RoutingButton to={link} onPress={startWorkout}>
              Start Workout
            </RoutingButton>
          )}
        </Paragraph>
      ) : (
        <Paragraph>Today is an active recovery day. Go for a run, do some stretching and/or some mobility work.</Paragraph>
      )}

      <Paragraph>
        You should consume <Text bold>{calories} calories</Text> and at least{' '}
        <Text bold>{recommendedNutrition.targetProtein} grams of protein</Text>. Calories can be split between fat and carbs to
        preference, recommendation is that 20-30% come from healthy fats to maintain hormonal balance.
      </Paragraph>
    </DashboardCard>
  );
}
