import { useEffect, useState } from 'react';
import { Box, Select, Switch, Text } from 'native-base';

import { useTrainee } from '../../domain/trainee';
import { DefaultLayout } from '../../components/Layouts';
import { Loader } from '../../components/Loader';
import { FormCard, InlineField } from '../../components/Forms';
import { StrengthStandardsComparison, SpeedtandardsComparison } from '../../components/Standards';
import { Gender, UnitsType, WeekDay } from '../../domain/constants';
import { Exercise } from '../../domain/exercises';
import { BulletPoint } from '../../components/Content';
import { NutritionInfo } from '../../components/Nutrition';
import { NutritionPreferences } from './components/NutritionPreferences';
import { NutritionType } from '../../domain/nutrition';
import { LiftingTargets, SpeedTargets } from '../../components/Targets';
import { useActiveProgram } from '../../domain/programs/activeProgram';
import { Destination, RoutingButton } from '../../utils/routing';

export default function ProgramSelection() {
  const [program, setProgram] = useState<string>();
  const {
    traineeLoaded,
    stats,
    preferences,
    setPreference,
    strengthStandards,
    strengthLevel,
    speedLevel,
    recommendedNutrition,
    recommendedPrograms,
    liftTargets,
    speedTargets,
    bodyFatTarget,
  } = useTrainee();
  const { initialiseProgram } = useActiveProgram();

  useEffect(() => {
    recommendedPrograms && setProgram(recommendedPrograms.bestMatch);
  }, [recommendedPrograms]);

  if (!traineeLoaded) {
    return <Loader />;
  }

  return (
    <DefaultLayout>
      <FormCard heading={'Strength'}>
        <Text marginBottom={2}>
          {`You are ranked as:`} <Text bold>{strengthLevel}</Text>
        </Text>
        {strengthStandards && (
          <StrengthStandardsComparison
            gender={Gender[stats.gender]}
            standards={{
              ...strengthStandards,
              [Exercise.Pullup]: { reps: stats.pullUps },
              [Exercise.Pushup]: { reps: stats.pushUps },
            }}
            unitsType={UnitsType[preferences.unitsType as keyof typeof UnitsType]}
          />
        )}
      </FormCard>

      <FormCard heading={'Speed'}>
        <Text marginBottom={2}>
          {`You are ranked as:`} <Text bold>{speedLevel}</Text>
        </Text>
        <SpeedtandardsComparison
          gender={Gender[stats.gender]}
          standards={{
            mileRun: stats.mileRun,
            fiveKmRun: stats.fiveKmRun,
          }}
        />
      </FormCard>

      {recommendedNutrition && (
        <FormCard heading={'Nutritional Recommendation'}>
          <NutritionInfo protocol={recommendedNutrition} />
          {[NutritionType.Cut, NutritionType.AggressiveCut].includes(recommendedNutrition.type) && (
            <InlineField>
              <Text maxWidth={'85%'}>
                <Text bold>More aggresive cut</Text>: for those who want to drop weight quicker and can handle lower calories.
              </Text>
              <Switch
                size="sm"
                isChecked={preferences.preferAggressiveCut}
                onValueChange={(checked: boolean) => setPreference('preferAggressiveCut', checked)}
              />
            </InlineField>
          )}
          <NutritionPreferences preferences={preferences} setPreference={setPreference} />
        </FormCard>
      )}

      {recommendedPrograms && (
        <FormCard heading={'Training Recommendation'}>
          <Box marginBottom={3}>
            <Text>You should focus on getting stronger and faster aiming for the following short term goals:</Text>
            {liftTargets && (
              <LiftingTargets
                liftTargets={liftTargets}
                unitsType={UnitsType[preferences.unitsType]}
                barbellIncrement={preferences.barbellIncrement}
              />
            )}
            {speedTargets && <SpeedTargets speedTargets={speedTargets} />}
            {bodyFatTarget && (
              <BulletPoint>
                <Text fontSize={'xs'}>Get your body fat down to {bodyFatTarget}%</Text>
              </BulletPoint>
            )}
          </Box>
          <Box marginBottom={3}>
            <Text fontSize={'xs'} italic>
              The most recommended program to achieve the above is selected, but you can also chose one of the other programs that
              match your level of strength and preferences and you will still see good results.
            </Text>
            <Select
              size={'md'}
              selectedValue={program}
              placeholder={'Select...'}
              onValueChange={(value: string) => setProgram(value)}
              minWidth={'100%'}
              textAlign={'left'}
              borderWidth={1}
            >
              {recommendedPrograms.suitablePrograms.map((program) => (
                <Select.Item
                  key={program.id}
                  value={program.id}
                  label={`${program.name}${program.id === recommendedPrograms.bestMatch ? ` (recommended)` : ''}`}
                />
              ))}
            </Select>
          </Box>
          <InlineField>
            <Text maxWidth={'85%'}>
              <Text bold>End workouts with a speed or endurance finisher</Text>. In true vigilante fashion, workouts will randomly
              end with a bodyweight exercise for reps and/or time, some sort of carry for distance and/or time or a 1 mile or 5k
              run for time.
            </Text>
            <Switch size="sm" isChecked />
          </InlineField>
        </FormCard>
      )}

      <RoutingButton
        to={Destination.Dashboard}
        onPress={() => initialiseProgram(program!, strengthLevel!, WeekDay[preferences.weekStart])}
      >
        {'Lets Go!'}
      </RoutingButton>
    </DefaultLayout>
  );
}
